import React, { useState, useRef } from 'react';

function Settings() {
  const [settings, setSettings] = useState({ width: '0px', lineHeight: '0' });

  const inputWidth = useRef(null);
  const inputLineHeight = useRef(null);

  function handleClick() {
    const width = (inputWidth.current.value || 0) + 'px';
    const lineHeight = inputLineHeight.current.value || 0;
    setSettings({ width: width, lineHeight: lineHeight });
  }

  return (
    <div>
      {/* <div>
        width: <input ref={inputWidth} type='text' />
        {settings.width}
      </div>
      <div>
        Line Height: <input ref={inputLineHeight} type='text' />
        {settings.lineHeight}
      </div>
      <button type='button' onClick={() => handleClick()}>
        Set
      </button> */}

      <table>
        <tbody>
          <tr>
            <td>日期：</td>
            <td>{new Date().toDateString()}</td>
          </tr>
          <tr>
            <td>完成的功能：</td>
            <td>渲染图片、渲染预定义定义样式的表格、渲染图表至DOCX</td>
          </tr>
          <tr>
            <td>未完成的功能：</td>
            <td>导出带有链接的目录、剩下的两个主题的数据、设置自定义表格样式（在应用到DOCX第三方库出现了问题，正在排查中）</td>
          </tr>
          <tr>
            <td>技术选型：</td>
            <td>React.js, DOCX.js, EChart.js</td>
          </tr>
          <tr>
            <td>选型理由：</td>
            <td>
              尝试过Html-to-docx.js 必须要渲染html代码才能导出DOCX，因此不选用；
              <br />
              尝试过officegen.js只能在node端实现功能，有代码直接导出图表功能；
              <br />
              尝试过docxtemplater.js该库非开源，需要预先提供docx编辑好的字段，可以生成word可编辑图表，需要购买商业lisence <br />
              DOCX不支持导出图表，只支持导出图片，相比较之下我选择DOCX.js来做这个poc，图和表格不需要渲染html代码即可生成，饼图需要在网页端渲染之后导出base64图片写入DOCX对象导出。
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export default Settings;
