import React from 'react';

function BasicInformation({ basicInfos }) {
  return (
    <div style={{ paddingLeft: '20px' }}>
      <h3>1. BasicInformation</h3>
      <table style={{ borderCollapse: 'collapse' }}>
        <tbody>
          {basicInfos.map((basicInfos, index) => (
            <tr style={{ lineHeight: '2', borderTop: '1px grey solid' }} key={index}>
              <td>{basicInfos.name}</td>
              <td>{basicInfos.value}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default BasicInformation;
