import React from 'react';

function Content({ contents }) {
  return (
    <div style={{ paddingLeft: '20px' }}>
      <div>Content</div>
      <ol style={{ color: 'skyblue' }}>
        {contents.map((content, index) => (
          <li key={index}>
            <a style={{color: 'skyblue'}} href={`#${index}`}>{content}</a>
          </li>
        ))}
      </ol>
    </div>
  );
}

export default Content;
