import React, { useState } from 'react';
import Header from '../components/Header';
import Content from '../components/Content';
import BasicInformation from './basicInformation';
import { project, basicInfos } from '../data/formattedData';

import generateDocx from '../utils/exportDocx.js';
import Echart from '../components/Echart';

function TableView() {
  const [chartSrc, setChartSrc] = useState({});
  const contents = ['Basic Information', 'Component Breakdown', 'Full Component List'];
  function getEchart(e) {
    setChartSrc(e);
  }
  return (
    <>
      <button onClick={() => generateDocx(chartSrc)}>Generate</button>
      <div id='html-content' style={{ width: '210mm', padding: '40px' }}>
        <Header fullName={project.fullName} />
        <Content contents={contents} />
        <BasicInformation basicInfos={basicInfos} />
        <Echart getChart={getEchart} />
      </div>
    </>
  );
}

export default TableView;
