import { saveAs } from 'file-saver';
import { Document, Packer, Paragraph, HeadingLevel, TextRun, ImageRun, Footer, PageNumber, AlignmentType, Table, TableCell, TableRow, BorderStyle, WidthType, HeightRule } from 'docx';
import { formattedData } from '../data/formattedData';
import logo from '../assets/images/scantistLogo.png'


async function generateDocx(chart) {
  const blob = logo;

  const emptyLines = (length) => Array.apply(null, {length}).map(() => new Paragraph(""))

  const rows = formattedData.basicInfos.data.map(
    (info) =>
      new TableRow({
        height: {
          height: 150,
          rule: HeightRule.ATLEAST
        },
        children: [
          new TableCell({
            children: [new Paragraph(info.name)],
            borders: {
              top: {
                style: BorderStyle.SINGLE,
                size: 3,
                color: '#333333',
              },
              bottom: {
                style: BorderStyle.SINGLE,
                size: 0,
                color: '#333333'
              },
              left: {
                style: BorderStyle.NIL,
                size: 0,
                color: '#333333'
              },
              right: {
                style: BorderStyle.NIL,
                size: 0,
                color: '#333333'
              },
            },
          }),
          new TableCell({
            children: [new Paragraph(info.value)],
            borders: {
              top: {
                style: BorderStyle.SINGLE,
                size: 3,
                color: '#333333',
              },
              bottom: {
                style: BorderStyle.SINGLE,
                size: 0,
                color: '#333333'
              },
              left: {
                style: BorderStyle.NIL,
                size: 0,
                color: '#333333'
              },
              right: {
                style: BorderStyle.NIL,
                size: 0,
                color: '#333333'
              },
            },
          }),
        ],
      })
  );

  const table = new Table({
    rows: [...rows],
  });

  const doc = new Document({
    sections: [
      {
        children: [
          ...emptyLines(12),
          new Paragraph({
            children: [
              new ImageRun({
                data: blob,
                transformation: {
                  width: 380,
                  height: 120,
                },
              }),
            ],
          }),
          new Paragraph({
            text: formattedData.header.title,
            heading: HeadingLevel.HEADING_1,
            border: {
              bottom: {
                color: 'auto',
                space: 1,
                value: 'single',
                size: 6,
              },
            },
          }),
          new Paragraph({
            text: formattedData.header.subtitle,
            heading: HeadingLevel.HEADING_2,
          }),
          new Paragraph(''),

          table,
          ...emptyLines(1),
          new Paragraph({
            children: [
              new ImageRun({
                data: Uint8Array.from(atob(chart.split(',')[1]), (c) => c.charCodeAt(0)),
                transformation: {
                  width: 400,
                  height: 250,
                },
              }),
            ],
          }),
        ],
        footers: {
          default: new Footer({
            children: [
              new Paragraph({
                alignment: AlignmentType.RIGHT,
                children: [
                  new TextRun({
                    children: [PageNumber.CURRENT, '/', PageNumber.TOTAL_PAGES],
                  }),
                ],
              }),
            ],
          }),
        },
      },
    ],
  });

  Packer.toBlob(doc).then((blob) => {
    console.log(blob);
    saveAs(blob, 'example.docx');
    console.log('Document created successfully');
  });
}

export default generateDocx;
